<template>
  <div class="container py-5">
    <div class="d-flex justify-content-between align-items-center">
      <div class="container p-0">
        <div class="page-header">
          <h2>Contact us</h2>
        </div>
      </div>
      <!-- <div class="container p-0">
				<div class="page-header-actions-block">
					

                <div class="web-form-actions justify-content-end d-flex">
                    <button class="btn btn-light btn-sm ml-2">Cancel</button>
                    <button class="btn btn-primary btn-sm ml-2">Send</button></div>
				</div>
			</div> -->
    </div>

    <div class="page_content">
      <div>
        <div role="form">
          <div id="introduction" class="text-muted">
            <div>
              <span style="color: rgb(112, 112, 112);"
                >Lets talk, drop your message and we will contact you the
                soonest</span
              >
            </div>
            <div><br /></div>
          </div>
          <hr style="opacity: 0.1;" />
          <div
            class="web-form-wrapper"
            data-web-form="contact-us"
            data-web-form-doctype="Communication"
            data-login-required="0"
            data-is-list="0"
            data-allow-delete="0"
          >
            <div class="form-layout">
              <div class="form-message text-muted small hidden"></div>
              <div class="form-page">
                <div class="row form-section visible-section mx-0">
                  <div class="section-body">
                    <div class="form-column col-sm-6">
                      <form>
                        <div
                          class="frappe-control input-max-width"
                          data-fieldtype="Data"
                          data-fieldname="sender_full_name"
                        >
                          <div class="form-group">
                            <div class="clearfix">
                              <label
                                class="control-label"
                                style="padding-right: 0px;"
                                >Full Name</label
                              >
                            </div>
                            <div class="control-input-wrapper">
                              <div class="control-input">
                                <input
                                  type="text"
                                  autocomplete="off"
                                  class="input-with-feedback form-control"
                                  maxlength="140"
                                  data-fieldtype="Data"
                                  data-fieldname="sender_full_name"
                                  placeholder=""
                                  v-model="state.fullName"
                                />
                              </div>
                              <div
                                class="control-value like-disabled-input"
                                style="display: none;"
                              ></div>
                              <p
                                class="help-box small text-muted hidden-xs"
                              ></p>
                            </div>
                          </div>
                        </div>
                        <div
                          class="frappe-control input-max-width"
                          data-fieldtype="Data"
                          data-fieldname="sender"
                        >
                          <div class="form-group">
                            <div class="clearfix">
                              <label
                                class="control-label"
                                style="padding-right: 0px;"
                                >Email</label
                              >
                            </div>
                            <div class="control-input-wrapper">
                              <div class="control-input">
                                <input
                                  type="text"
                                  autocomplete="off"
                                  class="input-with-feedback form-control"
                                  maxlength="140"
                                  data-fieldtype="Data"
                                  data-fieldname="sender"
                                  placeholder=""
                                  v-model="state.email"
                                />
                                <span class="text-danger" v-if="v$.email.$error"
                                  >* {{ v$.email.$errors[0].$message }}</span
                                >
                              </div>
                              <div
                                class="control-value like-disabled-input"
                                style="display: none;"
                              ></div>
                              <p
                                class="help-box small text-muted hidden-xs"
                              ></p>
                            </div>
                          </div>
                        </div>
                        <div
                          class="frappe-control input-max-width has-error"
                          data-fieldtype="Data"
                          data-fieldname="subject"
                        >
                          <div class="form-group">
                            <div class="clearfix">
                              <label
                                class="control-label"
                                style="padding-right: 0px;"
                                >Subject</label
                              >
                            </div>
                            <div class="control-input-wrapper">
                              <div class="control-input">
                                <input
                                  type="text"
                                  autocomplete="off"
                                  class="input-with-feedback form-control bold"
                                  maxlength="140"
                                  data-fieldtype="Data"
                                  data-fieldname="subject"
                                  placeholder=""
                                  v-model="state.subject"
                                />
                                <span
                                  class="text-danger"
                                  v-if="v$.subject.$error"
                                  >* {{ v$.subject.$errors[0].$message }}</span
                                >
                              </div>
                              <div
                                class="control-value like-disabled-input bold"
                                style="display: none;"
                              ></div>
                              <p
                                class="help-box small text-muted hidden-xs"
                              ></p>
                            </div>
                          </div>
                        </div>
                        <div
                          class="frappe-control"
                          data-fieldtype="Small Text"
                          data-fieldname="content"
                        >
                          <div class="form-group">
                            <div class="clearfix">
                              <label
                                class="control-label"
                                style="padding-right: 0px;"
                                >Message</label
                              >
                            </div>
                            <div class="control-input-wrapper">
                              <div class="control-input">
                                <textarea
                                  type="text"
                                  autocomplete="off"
                                  class="input-with-feedback form-control"
                                  data-fieldtype="Small Text"
                                  data-fieldname="content"
                                  placeholder=""
                                  style="height: 150px;"
                                  v-model="state.message"
                                ></textarea>
                                <span
                                  class="text-danger"
                                  v-if="v$.message.$error"
                                  >* {{ v$.message.$errors[0].$message }}</span
                                >
                              </div>
                              <div
                                class="control-value like-disabled-input for-description"
                                style="display: none;"
                              ></div>
                              <p
                                class="help-box small text-muted hidden-xs"
                              ></p>
                            </div>
                          </div>
                        </div>
                      </form>
                    </div>
                    <div class="form-column col-sm-6"><form></form></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="web-form-footer d-flex justify-content-end">
            <div>
              <button class="btn btn-danger btn-sm ml-2" @click="sendMessage">
                Send
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useVuelidate } from "@vuelidate/core";
import { required, email } from "@vuelidate/validators";
import { reactive, computed } from "vue";

export default {
  setup() {
    const state = reactive({
      fullName: "",
      email: "",
      subject: "",
      message: "",
    });

    const rules = computed(() => {
      return {
        email: { required, email },
        subject: { required },
        message: { required },
      };
    });

    const v$ = useVuelidate(rules, state);

    return {
      state,
      v$,
    };
  },
  methods: {
    sendMessage() {
      this.v$.$validate();
      if (!this.v$.$error) {
        alert("sent successfully");
        this.$router.push("/");
      } else {
        alert("please complete the missing fields");
      }
    },
  },
};
</script>

<style scoped>
.form-control:focus {
  border-color: rgba(255, 0, 0, 0.2) !important;
  box-shadow: 0 0 0 0.2rem rgba(255, 0, 0, 0.2) !important;
}
</style>
