span style="color: rgb(32, 31, 30); background-color: white;">What is your average man-hour rate for the freelance model?</span></li><li data-list="ordered"><span style="color: rgb(32, 31, 30); background-color: white;">When can you start?</span></li><li data-list="ordered"><span style="color: rgb(32, 31, 30); background-color: white;">Are you willing to work full-time if we achieved good business cooperation?</span></li></ol><div><br></div>',
        "dotnet-developer":
          '<div><strong style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">Job Description:</strong></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);"> Understand the business requirements.</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">&nbsp;</span><span style="background-color: rgb(255, 255, 255); color: inherit;">Participate in the technical design of the solutions</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">&nbsp;Implement the code and conduct the unit testing</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">&nbsp;Conduct code peer review</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">&nbsp;Develop the APIs and the connectors of the Middleware (LavaLoons product named LavaHub)</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">&nbsp;</span></div><div><strong style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">Job Requirements:</strong></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">* 2-3 .NET development experience</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">* <a class="text-danger text-decoration-none" href="http://ASP.NET">ASP.NET</a> core web and API development experience</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">* Azure Functions experience a plus</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);"> Able to integrate multiple data sources and databases into one system</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">* Typescript experience is required</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);"> Experience in front-end development of front-end technologies, such as JavaScript, jQuery, HTML5, and CSS3</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);">* Experience with VueJs 2 or 3 (or a similar SPA framework, but VueJs preferred)</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);"> Very good command of English Language</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);"> Teamwork spirit and good communication skills.</span></div><div><span style="background-color: rgb(255, 255, 255); color: rgb(54, 65, 76);"> More than 2 years of experience</span></div><div><br></div>',
      },
